<template>
    <div>
        <section class="hero has-background-primary-light">
            <div class="hero-body">
                <div class="columns">
                    <div class="column is-half-widescreen is-offset-one-quarter-widescreen">
                        <div>
                            <p>
                                The classifier is based on <strong>MobileBERT</strong> and trained on
                                <a>Kaggle Real Fake Dataset</a>. <strong>Onnx.js</strong> on WASM is used to
                                boost performance and allow real time inference.
                            </p>
                            <p>
                                Only English is supported at the moment.
                            </p>
                        </div>
                        <div class="mt-2">
                            <b-taglist>
                                <b-taglist attached>
                                    <b-tag type="is-success">Accuracy</b-tag>
                                    <b-tag>98%</b-tag>
                                </b-taglist>
                                <b-taglist attached class="ml-2">
                                    <b-tag type="is-success">Precision</b-tag>
                                    <b-tag>98%</b-tag>
                                </b-taglist>
                                <b-taglist attached class="ml-2">
                                    <b-tag type="is-success">Recall</b-tag>
                                    <b-tag>98%</b-tag>
                                </b-taglist>
                                <b-taglist attached class="ml-2">
                                    <b-tag type="is-success">F-Score</b-tag>
                                    <b-tag>98%</b-tag>
                                </b-taglist>
                            </b-taglist>
                            <router-link to="/details">View training details</router-link>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <section class="hero">
            <div class="hero-body">
                <div class="columns">
                    <div class="column is-half-widescreen is-offset-one-quarter-widescreen">
                        <div v-if="isModelLoading">
                            <p class="subtitle">
                                Model is loading...
                            </p>
                            <b-progress type="is-primary"></b-progress>
                        </div>
                        <div v-else>
                            <b-field>
                                <b-select placeholder="Example news" v-model="exampleId"
                                          expanded @input="setExample">
                                    <option value="custom">Custom</option>
                                    <option value="exampleFake">Example Fake</option>
                                    <option value="exampleReal">Example Real</option>
                                </b-select>
                            </b-field>
                            <b-field label="Title">
                                <b-input :disabled="isModelPredicting"
                                         v-model="title"></b-input>
                            </b-field>
                            <b-field label="Body">
                                <b-input :disabled="isModelPredicting"
                                         maxlength="1600" type="textarea" v-model="body"></b-input>
                            </b-field>
                            <b-button class="is-primary is-fullwidth" :disabled="isModelPredicting"
                                      :class="`${isModelPredicting ? 'is-loading' : ''}`"
                                      @click="predict">Check news
                            </b-button>
                            <b-message v-if="result !== undefined && !isModelPredicting"
                                       class="mt-4" :class="`is-${messageColor}`">{{ message }}
                            </b-message>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import examples from "@/assets/resources/examples.json";
import {loadModel} from "@/deeplearning/loadModel";
import {execute} from "@/deeplearning/execute";

const MODEL_FILEPATH = "/fake-news-detection-chrome-extension/models/mobilebert-fakenews-model.onnx";

export default {
    name: 'ModelExecution',
    props: {},
    data() {
        return {
            isModelLoading: true,
            isModelPredicting: false,
            session: undefined,
            model: undefined,
            title: "",
            body: "",
            result: undefined,
            exampleId: undefined,
        }
    },
    computed: {
        message() {
            return this.result === 1 ? "The news is real" : "The news is fake";
        },
        messageColor() {
            return this.result === 1 ? "success" : "danger";
        }
    },
    async created() {
        this.isModelLoading = true;
        const response = await fetch(MODEL_FILEPATH, {headers: {'Content-Type': 'application/octet-stream'}});
        const arrayBuffer = await response.arrayBuffer();
        this.model = await loadModel(arrayBuffer);
        this.isModelLoading = false;
    },
    methods: {
        async predict() {
            this.isModelPredicting = true;
            let text = this.title + " " + this.body;
            this.result = await execute(this.model, text);
            this.isModelPredicting = false;
        },
        setExample() {
            if (this.exampleId === undefined || this.exampleId === "custom") {
                this.title = "";
                this.body = "";
            } else if (this.exampleId === "exampleFake" || this.exampleId === "exampleReal") {
                this.title = examples[this.exampleId].title;
                this.body = examples[this.exampleId].body;
            }
        }
    }
}
</script>

