                        <p class="title mt-6">Dataset and Limitation</p>
                        <p>Our Fake News Detection Chrome Extension uses the <a href="https://www.kaggle.com/datasets/saurabhshahane/fake-news-classification" target="_blank">WELFake dataset</a>,
